*{
    padding: 0; margin: 0; box-sizing: border-box;
}
.ap-detail{
    background: #F9F9F9;
}
.ap-detail .col-md-4{
    font-family: Poppins;
    font-size: 18px;
    font-weight: 600;
    line-height: 24px;
    color: #6B6B6A;
}
.ap-detail-box{
    margin-top: 40px;
}

.ap-detail .col-md-12{
    height: 200px;
    border-radius: 4px;
}
.ap-detail .btn-primary{
    background: #EE9821;
    border-radius: 2px;
}