/* custom checkbox */

/*  Hide the inputs*/

.custom-checkbox input {
  display: none;
}

.custom-checkbox label {
  display: inline-block;
  cursor: pointer;
}

.custom-checkbox label span {
  position: relative;
  line-height: 22px;
}

.custom-checkbox label span:before,
label span:after {
  content: '';
}

.custom-checkbox label span:before {
    border: 1px solid #1da799;
    width: 12px;
    height: 12px;
    margin-right: 0px;
    display: inline-block;
    vertical-align: middle;
}

.custom-checkbox label span:after {
    background: #1da799;
    width: 6px;
    height: 6px;
    position: absolute;
    top: 6px;
    left: 3px;
    transition: 300ms;
    opacity: 0;
}
.custom-checkbox label input:checked+span:after {
  opacity: 1;
}
/* custom checkbox */

